import React, { Component } from 'react';
import './Login.css';
import { Link } from 'react-router-dom';
import axios from 'axios';


class Login extends Component {
  constructor(props){
    super(props)
    this.state={
      email:'',
      password:'',
    }
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e){
    this.setState({
        [e.target.name]: e.target.value,
    });
}

  createUserOrLogin(e, login){
    e.preventDefault();
    axios.post(`/api/${login}`, {email:this.state.email, password:this.state.password})
        .then((response)=>{
            if(response.data.success){
                this.props.history.push('/dashboard');
            }else{
                alert("Yo your password or maybe your email (all though I doubt it) is incorrect")
            }
        })
        .catch((err)=>{
            console.log(err)
        }) 
}

  render() {
    return (
      <div className="page1">
        <div>
          <img src="data:image/png;base64," alt = 'Login'/>
          </div>

        <p className="email">Username</p>
        <input type="text" value = {this.state.email} onChange={this.handleChange} name = 'email' className="input-username"/>
        <p className="password">Password</p>
        <input type="password" value = {this.state.password} onChange={this.handleChange} name = 'password' className="input-password"/>
        <div className="buttons">
        <Link to= '/dashboard'>
          <button className="login" onClick={(event)=>{this.createUserOrLogin(event, 'login')}}>Login</button>
          <button className="register" onClick={(event)=>{this.createUserOrLogin(event, 'register')}}>Register</button>
        </Link>
        </div>
        
      </div>
    );
  }
}

export default Login;


