import React, { Component } from 'react';
import './Step4.css';
import Header from '../../Header/Header';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators} from 'redux';
import {step4} from'../../../Redux/Actions/action';

class Step4 extends Component {
    constructor(props){

        super(props)
        this.state ={
            loan_amount:'',
            mortgage:'',
        }
        this.handleChange = this.handleChange.bind(this);
        this.step4 = this.step4.bind(this);
        
        
    }
    step4(){
        this.props.step4(this.state)
        this.props.history.push('/wizard/5')
    }
    handleChange(e){
        this.setState({
            [e.target.name]: e.target.value,
        });
    }
  render() {
    return (
        <div className="Step4">
            <Header/>
            <div className="StepPage">
                <div className="StepHeader">
                    <p className="headerTitle">Add new listing</p>
                    <Link to='/dashboard'>
                    <button className="cancelButton">Cancel</button>
                    </Link>
                </div>
                <div className="StepIdentification">
                    <p className="stepNumber">Step 4</p>
                    <div className="dotContainer">
                        <img src="data:image/png;base64," alt=""/>
                        <img src="data:image/png;base64," alt=""/>
                        <img src="data:image/png;base64," alt=""/>
                        <img src="data:image/png;base64," alt=""/>
                        <img src="data:image/png;base64," alt=""/>
                    </div>
                </div>
                <div className="stepContainer">
                    <div className="step4LoanAmount">
                        <p className="Loan">Loan Amount</p>
                    </div>
                    <input type="number" className="loanAmount" value = {this.state.loan_amount} onChange={this.handleChange} name = 'loan_amount'/>
                    <div className="step4MonthlyMortgage">
                        <p className="mortgage">Monthly Mortgage</p>
                    </div>
                    <input type="number" className="mortgage" value = {this.state.mortgage} onChange={this.handleChange} name = 'mortgage'/>
                    <div className="Step4ButtonContainer">
                        <Link to= '/wizard/3'>
                        <button className="previousStep4">Previous Step</button>
                        </Link>
                        <button className="nextStepStep4" onClick = {this.step4} >Next Step</button>
                    </div>
                </div>
            </div>
        </div>
    );
  }
}

function mapStateToProps({step4}){
	return {step4};
}
function mapDispatchToProps(dispatch){
	return bindActionCreators({step4}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Step4);  


